import React, { useState } from "react";
import { createFFmpeg, fetchFile } from "@flemist/ffmpeg.wasm-st";
import "./App.css";

function App() {
  const [videoSrc, setVideoSrc] = useState("");
  const [message, setMessage] = useState("Click Start to transcode");
  const ffmpeg = createFFmpeg({
    log: true,
  });
  const doTranscode = async (file: any) => {
    window.console.log("hhhh", file);
    setMessage("Loading ffmpeg-core.js");
    await ffmpeg.load();
    setMessage("Start transcoding");
    ffmpeg.FS("writeFile", "test.mp4", await fetchFile(file));
    await ffmpeg.run("-i", "test.mp4", "output.mp4");
    setMessage("Complete transcoding");
    const data = ffmpeg.FS("readFile", "output.mp4");
    setVideoSrc(
      URL.createObjectURL(new Blob([data.buffer], { type: "video/mp4" }))
    );
    const output = new File([data.buffer], "output.mp4", { type: "video/mp4" });
    console.log("hhhhh out", output);
    return output;
  };
  return (
    <div className="App">
      <p />
      <video src={videoSrc} controls></video>
      <br />
      <input
        type="file"
        onChange={(e) => {
          doTranscode(e.target.files?.item(0));
        }}
      />
      <br />
      <button onClick={doTranscode}>Start</button>
      <p>{message}</p>
    </div>
  );
}

export default App;
